import react, {Component} from 'react';
import Layout from '../../components/layout'
import {Message, MessageBody, MessageHeader, Delete} from 'bloomer'
import { Container, Title} from 'bloomer'
import web3 from '../../ethereum/web3'


export default class Dapps extends Component {
    state ={
        metamask: '',
        message: false,
        errorMessage: ''

    }
    // static async getInitialProps() {
    //     console.log("checking web3")
    
    //   return {metamask: web3}

    // }
    checkMetamask(w){
        console.log(w)
        if(w == ''){
            return <Message isColor="danger">
            <MessageHeader>
                <p>Metamask</p>
                <Delete />
            </MessageHeader>
            <MessageBody>
             You are not connected to Metamask. Please install Metamask to experience all the features of this page!!
            </MessageBody>
        </Message>

        }
       
           
    


   
    }
    render() {
        console.log("what is in ", web3)
        return ( <Layout>

            <Container>
            <Title isSize = {4}> The playground page </Title>
            {this.checkMetamask(web3)}
             <p> Ethereum playground </p>
           
            
             
              </Container> </Layout>)
        }

    }


